/* Variables */
$fontSizeH1: 86px
$fontSiezeH2: 60px
$fontSizeP: 20px
$fontSizeLink: 18px
$fontSizeNav: 16px
$fontSizeButton: 14px

.container main section.blog 
    display: flex
    flex-direction: column
    align-items: center
    padding: 0px
    position: relative
    width: 1180px
    height: 576px
    left: 129px
    top: 1020px
    .heading 
      display: flex
      flex-direction: row
      justify-content: space-between
      align-items: center
      padding: 0px
      position: static
      width: 1180px
      height: 64px
      left: 0px
      top: 0px
      h2
        position: static
        height: 64px
        left: 0px
        top: 0px
        font-family: 'Playfair Display'
        font-size: $fontSiezeH2
        line-height: 64px
        letter-spacing: -1px
        color: #2F2E41
      button
        position: static
        width: 115px
        height: 51px
        left: 1065px
        border: 1px solid #6C63FF
        box-sizing: border-box
        border-radius: 2px
        color: #6C63FF
        font-size: $fontSizeButton
        line-height: 19px
        background-color: #fff
        cursor: pointer
    .contentBlog 
      margin-top: 90px
      display: flex
      flex-direction: row
      align-items: flex-start
      padding: 0px
      position: static
      width: 1180px
      height: 422px
      left: 0px
      top: 154px
      .fusely, .introducing, .plan
        margin-right: 45px
        display: flex
        flex-direction: column
        align-items: flex-start
        padding: 0px
        position: static
        width: 363.33px
        height: 422px
        left: 0px
        top: 0px
        img
          margin-bottom: 26px
          position: static
          width: 363.33px
          height: 232px
          left: 0px
          top: 0px
          border-radius: 2px
        .contentArticle
          position: static
          width: 363.33px
          height: 164px
          left: 0px
          top: 258px
          h3 
            margin-bottom: 6px
            position: static
            width: 363.33px
            height: 32px
            left: 0px
            top: 0px
            font-weight: 600
            font-size: 22px
            line-height: 30px
            letter-spacing: -1px
            color: #2F2E41
          p 
            margin-bottom: 26px
            position: static
            width: 363.33px
            left: 0px
            top: 38px
            font-weight: normal
            font-size: 16px
            line-height: 26px
            color: #9893A3
          a 
            display: block
            position: static
            width: 100px
            height: 22px
            left: 0px
            top: 0px
            text-decoration: none
            text-align: center
            font-weight: 600
            font-size: $fontSizeLink
            line-height: 20px
            letter-spacing: -1px
            color: #2F2E41
            border-bottom: 10px solid rgba(197, 194, 255, 0.5)

@media (max-width: 768px) 
  .container main section.blog 
      align-items: flex-start
      position: absolute
      width: 688px
      height: 940px
      left: 0px
      top: 4580.31px
      .heading 
        width: 688px
      .contentBlog 
        margin-top: 90px
        display: flex
        flex-direction: column
        width: 688px
        height: 786px
        .fusely, .introducing, .plan
          margin-bottom: 45px
          display: flex
          flex-direction: row
          width: 688px
          height: 232px
          img
            width: 353px
          .contentArticle
            margin-left: 27px
            padding-top: 34px
            display: flex
            flex-direction: column
            align-items: flex-start
            position: static
            width: 308px
            left: 0px
            h3 
              width: 308px
            p 
              width: 308px
        .introducing, .plan
          .contentArticle
            padding-top: 21px

@media (max-width: 428px)
  .container main section.blog 
      position: absolute
      width: 388px
      height: 1437px
      left: 0px
      top: 3578.98px
      .heading 
        width: 388px
        height: 51px
        h2
          font-size: 34px
      .contentBlog 
        margin-top: 40px
        width: 388px
        height: 422px
        .fusely, .introducing, .plan
          margin-right: 0px
          margin-bottom: 40px
          display: flex
          flex-direction: column
          width: 388px
          height: 422px
          img
            width: 388px
          .contentArticle
            margin-left: 0px
            padding-top: 0px
            width: 388px
            left: 0px
            h3 
              width: 388px
            p 
              width: 388px
